/**
 * View d'Ã©dition d'un (et d'un seul) item.
 *
 * Peut-Ãªtre utilisÃ© dans de nombreux contextes. Le registre environnant doit Ãªtre au minimum de type IChainEnv,
 * un sous-registre sera instanciÃ© automatiquement pour complÃ©ter la configuration.
 *
 * Config attributes:
 * - wsp --> wspCd
 * - src-ref --> srcRef
 * - editor-key --> editorKey
 * - editor-config (json) --> editorConfig
 * - wedmgr-config (json) --> wedMgrConfig
 */
export interface OItemXmlEdInit {
	reg?: IReg<IChainEnv & IUiEnv> | IReg<IWspUiEnv> | IReg<IItemTypeUiEnv> | IReg<IItemUiEnv>
	wspCd?: wspCd
	srcRef?: srcRef
	editorKey?: string
	editorConfig?: OWedEditorBoxConfig
	wedMgrConfig?: OWedManagerConfig
	writePerms?: string | string[]
	datasEditor?: IDatasEditor
	noNotifIfReadOnly?: boolean
	initItemReg?: (reg: IReg<IItemUiEnv>, itemEditor: ItemXmlEd) => void
	saveOnClose?: boolean
	/** lastDatas pour l'initialisation (la key est construite auto: "ed:"+srcRef). */
	lastDatas?: JLastDatas
}

export interface ItemXmlEd extends BaseElementAsync {
	initialize(init: OItemXmlEdInit): this;
}

export class ItemXmlEd extends BaseElementAsync implements IView, IViewContainer {

	/**
	 * L'env de l'Ã©diteur doit Ãªtre de niveau IItemUiEnv en particulier pour le securityCtx
	 * et l'eval des perms qui pourraient Ãªtre rÃ©alisÃ©es dans le wed.
	 */
	reg: IReg<IItemUiEnv>;

	configs: OItemXmlEdInit;

	view: WedEditorBox;

	protected _saveAct: SaveAction;

	isClosed(): boolean {return !this.view}

	/**
	 * @param leaveState Si la place est partagÃ©e (non instanciÃ©e dans l'itemEditor),
	 *    passe l'item Ã©ditÃ© Ã  l'Ã©tat leaveState (gÃ©nÃ©ralement r(ead) ou l(isten)).
	 */
	closeEditor(leaveState?: EWspWorkingSt) {
		if (!this.view) return;
		VIEWS.onViewHidden(this.view, true);
		if (this.configs.saveOnClose && this.isDirty()) this.save();
		this.view.wedMgr.killEditor();
		if ((this.configs.reg.env as IWspsLivePlacePointer).place !== this.reg.env.place) {
			//On a crÃ©Ã© notre place en interne.
			this.reg.env.place.closePlace();
			this.reg.env.place = null;
		} else {
			//place partagÃ©e.
			if (leaveState != null) this.reg.env.place.setState(WSP.buildWspRef(this.configs.wspCd, this.configs.srcRef), leaveState);
		}
		this.view = null;
	}

	_refresh() {
		super._refresh();
		if (this.view) {
			const wedMgr = this.view.wedMgr;
			const docHolder = wedMgr.docHolder as IPlaceDocHolder;
			if (docHolder.isAvailable) {
				if (!wedMgr.setReadOnly('locked', docHolder.house.lockedBy != null, 'perms', !this.reg.hasPerm(this._writePermToCheck()))) {
					//Le Refresh peut Ãªtre dÃ» Ã  un chgt de roles qui peuvent avoir un impact uniquement au sein de l'Ã©diteur
					WEDLET.refreshEditMode(wedMgr.rootWedlet);
				}
			}
		}
	}

	protected _writePermToCheck() {
		return this.configs.writePerms || "action.itemXmlEd#edit";
	}

	isDirty(): boolean {
		return this.view ? this.view.wedMgr.docHolder.house.isDirty : false;
	}

	async save(): Promise<void> {
		return this._saveAct.execute(this.view);
	}

	protected _initialize(init: OItemXmlEdInit): Promise<void> {
		this.style.display = "contents";
		this.configs = init || {};
		if (!this.configs.reg) this.configs.reg = this.findReg(init);
		return this.launchEditor();
	}

	buildInitFromAtts(init: OItemXmlEdInit): OItemXmlEdInit {
		init = super.buildInitFromAtts(init);
		if (this.hasAttribute('wsp')) init.wspCd = BASIS.extractAttr(this, 'wsp');
		if (this.hasAttribute('src-ref')) init.srcRef = BASIS.extractAttr(this, 'src-ref');
		if (this.hasAttribute('editor-key')) init.editorKey = BASIS.extractAttr(this, 'editor-key');
		if (this.hasAttribute('editor-config')) init.editorConfig = JSON.parse(BASIS.extractAttr(this, 'editor-config'));
		if (this.hasAttribute('wedmgr-config')) init.wedMgrConfig = JSON.parse(BASIS.extractAttr(this, 'wedmgr-config'));
		return init;
	}

	protected async launchEditor(): Promise<void> {
		let confReg = this.configs.reg as IReg<IItemUiEnv>; //On va complÃ©ter confReg pour obtenir un IReg<IItemUiEnv>
		const originalEnv = confReg.env as IItemUiEnv;

		const place = originalEnv.place || originalEnv.universe.wspServer.wspsLive.newPlace();

		let wsp = originalEnv.wsp;
		if (!wsp) {
			const chain = originalEnv.universe;
			if (!chain || !this.configs.wspCd) throw Error("Universe or wspCd not found");
			wsp = await place.getWsp(this.configs.wspCd).waitForAvailable(this);
		} else if (!wsp.isAvailable) {
			await wsp.waitForAvailable(this);
		}

		let srcRef: srcRef = this.configs.srcRef;
		if (!srcRef) {
			srcRef = ITEM.srcRefInContext(originalEnv.longDesc);
			if (originalEnv.itemType?.getFamily() === EItemTypeFamily.res) {
				//Edition des metas d'un item.
				let targetUri = originalEnv.itemType.getMainXmlSrcUri(originalEnv.longDesc);
				if (SRC.isSrcId(srcRef)) {
					//On est en contexte db hors cas spÃ©ciaux (cf srcRefInContext), on tente de rÃ©cupÃ©rer l'id
					srcRef = (await WSP.fetchSrcId(originalEnv.wsp, this, targetUri, true)) || targetUri;
				} else {
					srcRef = targetUri;
				}
				//console.log(":::srcRef::::", srcRef);
			}
		}

		const docHolder = await place.newDocHolder(wsp, srcRef);
		if (!docHolder) throw Error("Source not found");

		const itemType = originalEnv.itemType || docHolder.house.itemType;

		if (confReg.env.wsp !== wsp) confReg = REG.createSubRegMixed(confReg, wsp.reg);
		if (confReg.env.itemType !== itemType) confReg = REG.createSubRegMixed(confReg, itemType.reg);

		//Reg final de niveau IItemUiEnv
		this.reg = REG.createSubReg(confReg);
		this.reg.env.place = place;
		if (!this.reg.env.longDesc) {
			const longDesc = this.reg.env.longDesc = await wsp.fetchLongDesc(srcRef);
			this.reg.env.securityCtx = SEC.createSub(wsp.reg.env.securityCtx, longDesc.srcRoles, longDesc.srcRi, longDesc);
		}

		const editorConfig = Object.assign({}, this.configs.editorConfig) as OWedEditorBoxConfig;
		const wedMgrConfig = Object.assign({}, this.configs.wedMgrConfig) as OWedManagerConfig;

		if (!wedMgrConfig.fetchContentThreshold) wedMgrConfig.fetchContentThreshold = 6000;

		if (!editorConfig.metaBarMode) editorConfig.metaBarMode = 'available';

		if (!wedMgrConfig.dataTransferAnalyzer) wedMgrConfig.dataTransferAnalyzer = WspDataTransferWedAnalyzer.SINGLETON;

		const dataEditor = this.configs.datasEditor || itemType.getEditor(this.configs.editorKey);
		if (!dataEditor) throw Error("No editor found: " + this.configs.editorKey);

		this.view = new WedEditorBox();

		if (dataEditor.help) (await import("back/edit/wed/features/helpBar.js")).initHelpOnEditor(dataEditor.help, this.reg, editorConfig, wedMgrConfig, this.view)

		if (dataEditor.initReg) await dataEditor.initReg(this.reg, this.view);
		if (dataEditor.overrideConfigs) await dataEditor.overrideConfigs(editorConfig, wedMgrConfig, this.view);
		if (this.configs.initItemReg) this.configs.initItemReg(this.reg, this);

		editorConfig.reg = this.reg;
		const parentDatas = this.configs.lastDatas;
		editorConfig.lastDatasKey = "ed:" + srcRef;
		const lastDatas = parentDatas && parentDatas[editorConfig.lastDatasKey];
		if (lastDatas) {
			editorConfig.lastDatas = lastDatas;
			parentDatas[editorConfig.lastDatasKey] = null; //on tue les lastDatas de cet Ã©diteur en cas d'une fermeture et d'une rÃ©ouverture dans cette session.
		}

		//chargement asynchrone en // des librairies standard wed et des wedModels
		this.view.addAsyncLib(boxTagsDefined);
		this.view.addAsyncLib(txtTagsDefined);
		this.view.addAsyncLib(ptrItemTagsDefined);
		const mainEndP = itemType.getWed(dataEditor.mainWed);
		this.view.addAsyncLib(WED.loadWedModel(mainEndP, mainEndP).then((wed) => this.view.wedModel = wed));
		if (dataEditor.outlineWed) {
			const outlineEndP = itemType.getWed(dataEditor.outlineWed);
			this.view.addAsyncLib(WED.loadWedModel(outlineEndP, outlineEndP).then((wed) => this.view.outlineWedModel = wed));
		}
		if (dataEditor.previewWed) {
			const altviewEndP = itemType.getWed(dataEditor.previewWed);
			this.view.addAsyncLib(WED.loadWedModel(altviewEndP, altviewEndP).then((wed) => this.view.previewWedModel = wed));
		}

		//Gestion graphique du lock et de l'absence de droit en Ã©criture
		const lockBtn = new LockAction();

		wedMgrConfig.readOnlyCauses = wedMgrConfig.readOnlyCauses ? wedMgrConfig.readOnlyCauses.concat() : [];
		if (itemType.datas.readOnly) wedMgrConfig.readOnlyCauses.push("itemType");
		if (docHolder.house.lockedBy) wedMgrConfig.readOnlyCauses.push("locked");
		else if (!this.reg.hasPerm(this._writePermToCheck())) {
			if (!this.configs.noNotifIfReadOnly) {
				if (!this.reg.hasSystemRights(this._writePermToCheck())) {
					if (!ITEM.isHistoryOrTrashUri(this.reg.env.longDesc.srcUri)) {
						POPUP.showNotifInfo("Ce contenu n'est pas Ã©ditable.", this);
					}
				} else {
					POPUP.showNotifForbidden("Vous ne disposez pas des droits pour modifier ce contenu.", this);
				}
			}
			wedMgrConfig.readOnlyCauses.push("perms");
		}
		docHolder.houseLstn.on("onLock", this.refresh.bind(this));
		docHolder.houseLstn.on("onPermChange", (house) => {
			this.reg.env.securityCtx = SEC.createSub(wsp.reg.env.securityCtx, house.srcFields.srcRoles, house.srcFields.srcRi, house.srcFields);
			this.refresh();
		});

		this._saveAct = new SaveAction(this, docHolder);
		this.reg.addToList("actions:wed:commonbar:start", "save", 1, this._saveAct, 0);
		this.reg.addToList("actions:wed:commonbar:start", "lock", 1, lockBtn);

		if (isHistoEditEnv(originalEnv) && originalEnv.infoBroker) {
			wedMgrConfig.histoEditPointsMgr = originalEnv.histoEditMgr;
			wedMgrConfig.histoEditHolder = new EditHolder(SRC.srcRef(this.reg.env.longDesc), originalEnv.infoBroker);
			const act = new HistoEditAction(originalEnv.histoEditMgr);
			this.reg.addToList("actions:wed:commonbar:start", "histoEdit", 1, act, 55);
			this.reg.addToList("accelKeys:wed:global", "q-accel-shift", 1, act);
		}

		this.reg.addToList("accelKeys:wed:global", "s-accel", 1, this._saveAct);
		this.reg.addToList("accelKeys:wed:global", "i-accel-alt-shift", 1, viewXmlAction);


		this.reg.addToList("actions:wed:commonbar:end", "outline", 1, new ShowOutlineAction(), 51);
		this.reg.addToList("actions:wed:commonbar:end", "preview", 1, new ShowPreviewAction(), 61);
		//this.reg.addToList("actions:wed:commonbar:end", "metaBar", 1, new ShowMetaBarAction());

		this.reg.registerSvc("wedTxtSelMgrClass", 1, WspTxtSelMgr);

		this.view.initialize(editorConfig);
		if (await this.view.initFromDocHolder(docHolder, wedMgrConfig)) {
			this.appendChild(this.view);
			place.setState(docHolder.house.wspRef, EWspWorkingSt.r); //Toujours visible.
		} else {
			this.view = null;
			this._saveAct = null;
			throw Error("Init editor failed");
		}
	}

	onViewHidden(closed?: boolean) {
		if (this.view && this.reg) (this.reg.env as IHistoEditEnv).histoEditMgr?.onEditorLooseFocus(this.view.wedMgr.config.histoEditHolder);
		if (closed) this.closeEditor();
		else VIEWS.onViewHidden(this.view, closed);
	}

	visitViews(visitor: (view: IView) => any): any {
		if (this.view) return visitor(this.view);
	}

	visitViewsAsync(visitor: (view: IView) => Promise<any>, options?: OViewVisitOptions): Promise<any> {
		return this.view ? visitor(this.view) : Promise.resolve(undefined);
	}

}

customElements.define("wsp-item-xmled", ItemXmlEd);


class SaveAction extends Action<IWedEditor> {

	saving: boolean;

	button: ActionBtn<IWedEditor>;

	constructor(readonly itemEditor: ItemXmlEd, docHolder: IPlaceDocHolder) {
		super('save');
		this.setIcon("/@skin@/edit/wed/save.svg");
		this.setLabel("Enregistrer");
		docHolder.houseLstn.on('dirtyChange', () => {if (this.button) this.button.refresh()});
		docHolder.houseLstn.on('onSave', (state: 'saving' | 'saved' | 'saveFailed', e?: any): void | Promise<void> => {
			if (!this.button) return;
			this.button.classList.toggle('saving', state === 'saving');
			if (state === 'saveFailed') {
				if (e instanceof RespError && e.response.status === EHttpStatusCode.forbidden) {
					const wedMgr = itemEditor.view.wedMgr;
					wedMgr.writeRangeToClipboard(XA.newRangeAround(wedMgr.rootWedlet.wedAnchor)).then(() => {
						POPUP.showNotifForbidden("Vous ne disposez plus des droits en Ã©dition, vos modifications annulÃ©es ont Ã©tÃ© copiÃ©es dans le presse-papier.", itemEditor, {autoHide: 20000});
					}).catch(() => {
						POPUP.showNotifForbidden("Vous ne disposez plus des droits en Ã©dition, vos modifications ont dÃ» Ãªtre annulÃ©es.", itemEditor, {autoHide: 20000});
					});
					return docHolder.place.abortChangesAndReload(docHolder.house);
				} else {
					POPUP.showNotifError("Ãchec Ã  l'enregistrement, connexion au serveur impossible. Attention, vous risquez de perdre vos modifications.", itemEditor);
				}
				this.saving = false; //pour que le refresh du bouton prenne en compte l'Ã©tat dirty dÃ» Ã  l'Ã©chec cf isEnabled(ctx: IWedEditor)
			}
		})
	}

	/** Pas de bouton save pour les sous-editeurs. */
	buildCustomButton(ctx: IWedEditor, uiContext: EButtonUiContext, parent?: Element): Element | null | undefined {
		return this.button ? null : undefined;
	}

	initButtonNode(buttonNode: ActionBtn<IWedEditor>, ctx: IWedEditor) {
		this.button = buttonNode;
		this.itemEditor.reg.installSkin("wsp-item-xmled/saveBtn", buttonNode.shadowRoot);
	}

	isVisible(ctx: IWedEditor): boolean {
		if (ctx.wedMgr.readOnly) return false;
		return super.isVisible(ctx);
	}

	isEnabled(ctx: IWedEditor): boolean {
		if (this.saving) return false;
		return super.isEnabled(ctx) && ctx.wedMgr.docHolder.house.isDirty;
	}

	async execute(ctx: IWedEditor, ev?: Event) {
		const docHolder = ctx.wedMgr.docHolder as IPlaceDocHolder;
		if (!docHolder.save) return; //Pas un IPlaceDocHolder : cas d'un DocHolder local dÃ©tachÃ© de l'item : metas en crÃ©ation => abandon ddu ctrl+S.
		try {
			this.saving = true;
			await docHolder.save();
		} finally {
			this.saving = false;
		}
	}
}

REG.reg.registerSkin("wsp-item-xmled/saveBtn", 1, /* language=CSS */ `
	:host(.saving) {
		--icon-color: red;
	}
`);


class LockAction extends Action<IWedEditor> {

	constructor() {
		super('lock');
		this.setIcon("/@skin@/wsp/widgets/itemEditor/editing.svg");
		this.setDescription("Ce contenu n'est pas Ã©ditable car il est actuellement en cours d'Ã©dition.");
	}

	isVisible(ctx: IWedEditor): boolean {
		return this.getLockedBy(ctx) != null;
	}

	getLabel(ctx: IWedEditor) {
		const lock = this.getLockedBy(ctx);
		return lock ? "ÃditÃ© par " + lock.account : null;
	}

	initButtonNode(buttonNode: ActionBtn<IWedEditor>, ctx: IWedEditor) {
		buttonNode._hideLabel = false;
		buttonNode.tabIndexCustom = -2;
	}

	getLockedBy(ctx: IWedEditor) {
		return (ctx.wedMgr.docHolder as IPlaceDocHolder).house.lockedBy;
	}
}

